export default {
  data() {
    return {};
  },
  transition: 'page',

  computed: {
    city: {
      get() {
        return this.$store.getters.city;
      },
      set(value) {
        this.$store.commit("updateCity", value);
      },
    },
  },

  methods: {
    fetchData(event) {
      this.$store.dispatch("fetchWeather");
      event.target.value = "";
      this.$store.commit("clearInput");
    },
  },
};
